@import 'libs/animate.min.css';

body {
  margin: 0;
  padding: 0;
  font-size: 16px;
}

body .block {
  width: 50%;
  min-height: 100px;
  background-color: red;
  padding: 1px;
}

img {
  max-width: 100%;
  height: auto;
}

.ov-h {
  overflow: hidden;
}

/* 
	@media screen and (min-width: 320px) and (max-width: 740px){
	/////
	}
	height: 100vh;
	overflow-y: scroll;
 */

/*
em - пропорцилнальная еденица, считается 
	от размера шрифта родителя
rem - пропорционально берет размер от корневого узла
	документа!
vw - ширина окна браузера
vh - высота окна браузера
vmin - берет меньшее между высотой и шириной экрана 
	и считает по принципу vw/vh
vmax - берет большее между высотой и шириной экрана 
	и считает по принципу vw/vh
 */

ul {
  font-size: 2em;
  /* 2 * fon-size(родителя) */
}

.for-rem {
  font-size: 2rem;
  /* 2 * font-size(html, body, :root) */
}

.big {
  font-size: 7.5vw;
}

.preloader {
  position: fixed;
  background-color: #fff;
  top: 0;
  left: 0;
  width: 100%;
  bottom: 0;
}

.wrap {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 300px;
  height: 300px;
}

.cup {
  position: absolute;
  width: 100px;
  height: 100px;
  left: 50%;
  bottom: 0;
  left: 0;
}

.teapot {
  position: absolute;
  width: 100px;
  height: 100px;
  top: 0;
  right: 170px;
  animation: pot-rotate 5s ease-in;
}

.teapot:before {
  content: '';
  display: block;
  border-bottom: 5px dashed #2D1B01;
  position: absolute;
  top: 20px;
  right: 95px;
  width: 0;
  animation: cofee-stream 2s ease-out;
  animation-delay: 2s;
}

@keyframes pot-rotate {
  0 {
    transform: rotate(0);
  }

  30% {
    transform: rotate(-90deg);
  }

  70% {
    transform: rotate(-90deg);
  }

  100% {
    transform: rotate(0);
  }
}

@keyframes cofee-stream {
  0 {
    width: 0px;
  }

  30% {
    width: 0;
  }

  35% {
    width: 0;
  }

  65% {
    width: 150px;
  }

  70% {
    width: 150px;
  }

  100% {
    width: 0px;
  }
}

@media screen and (max-width: 750px) {
  .block {
    width: 75%;
    margin: 0 auto;
  }
}

@media screen and (max-width: 648px) {
  .big {
    font-size: 24px;
  }
}

@media screen and (max-width: 500px) {
  body .block {
    width: 100%;
  }
}

@media screen and (max-width: 400px) {
  body .block {
    background-color: cyan;
  }
}